.container-shopping-list {
    background-color: whitesmoke;
}

.cart-icon {
    margin-left: 1.8em;
}

.shop-icon {
    margin-right: 0.5em;
}

.card {
    align-items: center;
    width: 15em;
    height: auto;
    margin: 1em;
}

.card img {
    width: 15em;
    height: auto;
}

.box {
    display: flex;
    flex-wrap: wrap;
    position:relative;
}

.cart {
    margin-top: 3em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.btn-add-cart {
    width: 100%;
}

.cart-item {
    border-style: double;
    display: flex;
    margin: 2em;
    padding: 2em;
}

.cart-item img{
    margin: auto;
    height: 15em;
    position: center;
    vertical-align: middle;
}

.item-desc {
    display: inline-block;
    margin-left: 3em;
    margin-right: 2em;
    vertical-align: middle;
}

.item-desc b {
    font-family: 'Times New Roman', Times, serif;
    position: relative;
    margin: 0.5em;
}

.add-remove b {
    margin-left: 2em;
    margin-right: 2em;
}

.btn-remove-from-cart {
    margin-left: 0.3em;
}

.cart-detail {
    border-style: groove;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.cart-detail h5 {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 0.5em;
}

.filter-panel {
    align-self: flex-start;
    text-align: center;
    display: block;
    border-style: groove;
    min-height: 10em;
    margin-top: 2em;
    padding-right: 2em;
    padding-left: 2em;
}

.filter-panel h5 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin-top: 1.4em;
    margin-bottom: 1.5em;
}

@media screen and (max-width:600px) {
    .card {
        display: flex;
        width: 25em;
    }

    .card img {
        width: 15em;
        height: auto;
    }

    .btn-remove-from-cart {
        margin-left: 3.3em;
        margin-top: 1em;
    }

    .item-desc {
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        position: relative;
        margin-left: 1.5em;
    }

    .form-control {
        width: 68%;
        margin-right: 1em;
    }
}